import React, { Component } from "react";
import { connect } from "react-redux";
import { addToCart } from "../actions/auth";
var data = {};
class Details extends Component {
  state = {};
  load = () => {
    var img = this.props.products.map((item) => item.img);
    var title = this.props.products.map((item) => item.title);
    var desc = this.props.products.map((item) => item.desc);
    var price = this.props.products.map((item) => item.price);
    var all = [...img, ...title, ...desc, ...price];
    var id = this.props.products.map((item) => item.id);
    //console.log(all);
    var n = all.length / 4;
    var token = this.props.match.params.id;
    var ind = "";

    for (var i = 0; i < id.length; i++) {
      if (token == id[i]) {
        ind = i;
      }
    }
    data.id = token;
    data.img = all[ind];
    data.title = all[ind + n];
    data.desc = all[ind + n + n];
    data.price = all[ind + n + n + n];
    //console.log(data);
  };
  /*  handleClick = (id) => {
    this.props.dispatch(addToCart(id));
    alert("successfully added to cart");
    this.props.history.push("/cart");
  }; */
  render() {
    return (
      <div>
        {this.load()}
        <div className="thumbnail text-center">
          <img
            className="selfstyle"
            /* style={{ width: "300px", height: "300px" }} */
            src={data.img}
            alt={data.title}
          />
          <br />
          {/* <span style={{ display: "none" }}>{(id = data.id)}</span> */}

          <span className="">{data.title}</span>

          <p>{data.desc}</p>

          <p>
            <b>Price: â‚¹{data.price}/-</b>
          </p>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    items: state.cartReducer.items,
    products: state.cartReducer.filteredItems,
  };
};
export default connect(mapStateToProps)(Details);
